import GridBox from './GridBox'

interface Content {
    photo: string
}

interface Props {
    GridContent: Array<Content>
}


const Grid:React.FC<Props> = ({GridContent}) => {
    return (
        <ul>
            {GridContent.map((content: Content, index) => 
                <GridBox index={index} BoxContent={content}></GridBox>    
            )}
        </ul>
    )
};

export default Grid;